}
.button {
  display: block;
  line-height: 18px;
  padding: 11px 44px;
  background-color: #68b738;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
  font-size: 14px;
  text-decoration: none;
  width: 100%;
  border: none;
}

.button:hover {
  background-color: #5eaa2f;
}

.button:active {
  color: rgba(255, 255, 255, 0.3);
}

.button--more {
  color: #444444;
  padding: 11px;
  background-color: #f2f2f2;
}

.button--more:hover {
  background-color: #ebebeb;
}

.button--more:active {
  color: rgba(68, 68, 68, 0.3);
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

